<template>
  <div class="seller-rank-container" v-loading="loading">
    <el-card class="box-card">
      <div class="space"></div>
      <!--  -->
      <el-form :inline="true" :model="search">
        <el-form-item>
          <el-form-item label="" prop="saleZoneCode">
            <el-select size="small" v-model="search.saleZoneCode" placeholder="" @change="handleSaleZoneCodeChange">
              <el-option
                  v-for="(item,index) in saleZone"
                  :key="index"
                  :label="item.zoneName"
                  :value="item.zoneCode">
              </el-option>
            </el-select>
          </el-form-item>
        </el-form-item>
        <el-form-item>
          <el-form-item label="" prop="saleZoneCode">
            <el-select size="small" v-model="search.provinceCode" placeholder="">
              <el-option
                  v-for="(item,index) in saleZoneProv"
                  :key="index"
                  :label="item.name"
                  :value="item.code">
              </el-option>
            </el-select>
          </el-form-item>
        </el-form-item>
        <el-button size="small" type="primary" @click="getSellerRank"></el-button>
        <el-button size="small" v-if="showBtn" @click="handleClickALL('allZone')"></el-button>
        <el-button size="small" v-if="showBtn" @click="handleClickALL('allProv')"></el-button>
      </el-form>
    </el-card>
    <div style="height: 20px"></div>
    <el-card>
      <div class="form-container">
        <div class="rank-form">
          <div class="title"></div>
          <template v-if="!showMore1">
            <el-table
                :data="fansNumRankList"
                style="width: 300px;height: 440px">
              <el-table-column
                  align="center"
                  type="index"
                  width="50">
              </el-table-column>
              <el-table-column
                  align="center"
                  prop="totalFans"
                  label=""
              >
              </el-table-column>
              <el-table-column
                  show-overflow-tooltip
                  align="center"
                  prop="displayName"
                  label="/"
              >
              </el-table-column>
            </el-table>
            <div class="more" @click="showMore1 = true"></div>
          </template>
          <template v-else>
            <el-table
                v-if="showMore1"
                :data="fansNumRankList"
                max-height="600"
                style="width: 300px;min-height: 440px">
              <el-table-column
                  align="center"
                  type="index"
                  width="50">
              </el-table-column>
              <el-table-column
                  align="center"
                  prop="totalFans"
                  label=""
              >
              </el-table-column>
              <el-table-column
                  align="center"
                  prop="displayName"
                  label="/"
              >
              </el-table-column>
            </el-table>
            <div class="more" @click="showMore1 = false"></div>
          </template>
        </div>
        <div class="rank-form">
          <div class="title"></div>
          <template v-if="!showMore2">
            <el-table
                :data="fansScanNumRankList"
                style="width: 300px;height: 440px">
              <el-table-column
                  align="center"
                  type="index"
                  width="50">
              </el-table-column>
              <el-table-column
                  align="center"
                  prop="totalScanCount"
                  label=""
              >
              </el-table-column>
              <el-table-column
                  show-overflow-tooltip
                  align="center"
                  prop="displayName"
                  label="/"
              >
              </el-table-column>
            </el-table>
            <div class="more" @click="showMore2 = true"></div>
          </template>
          <template v-else>
            <el-table
                v-if="showMore2"
                :data="fansScanNumRankList"
                max-height="600"
                style="width: 300px;min-height: 440px">
              <el-table-column
                  align="center"
                  type="index"
                  width="50">
              </el-table-column>
              <el-table-column
                  align="center"
                  prop="totalScanCount"
                  label=""
              >
              </el-table-column>
              <el-table-column
                  show-overflow-tooltip
                  align="center"
                  prop="displayName"
                  label="/"
              >
              </el-table-column>
            </el-table>
            <div class="more" @click="showMore2 = false"></div>
          </template>
        </div>
        <div class="rank-form">
          <div class="title"></div>
          <template v-if="!showMore3">
            <el-table
                :data="fansScanSnNumRankList"
                style="width: 300px;height: 440px">
              <el-table-column
                  align="center"
                  type="index"
                  width="50">
              </el-table-column>
              <el-table-column
                  align="center"
                  prop="totalScanCount"
                  label=""
              >
              </el-table-column>
              <el-table-column
                  align="center"
                  prop="snName"
                  label=""
              >
              </el-table-column>
            </el-table>
            <div class="more" @click="showMore3 = true"></div>
          </template>
          <template v-else>
            <el-table
                v-if="showMore3"
                :data="fansScanSnNumRankList"
                max-height="600"
                style="width: 300px;min-height: 440px">
              <el-table-column
                  align="center"
                  type="index"
                  width="50">
              </el-table-column>
              <el-table-column
                  align="center"
                  prop="totalScanCount"
                  label=""
              >
              </el-table-column>
              <el-table-column
                  show-overflow-tooltip
                  align="center"
                  prop="snName"
                  label=""
              >
              </el-table-column>
            </el-table>
            <div class="more" @click="showMore3 = false"></div>
          </template>
        </div>

      </div>
    </el-card>
  </div>
</template>

<script>
const  RankData =  {
  "fansScanSnNumRankList": [ //
    {
      "totalScanCount": 705,
      "snName": "1"
    },
    {
      "totalScanCount": 411,
      "snName": "2"
    },
    {
      "totalScanCount": 203,
      "snName": "3"
    },{
      "totalScanCount": 705,
      "snName": "1"
    },
    {
      "totalScanCount": 411,
      "snName": "2"
    },
    {
      "totalScanCount": 203,
      "snName": "3"
    },{
      "totalScanCount": 705,
      "snName": "1"
    },
    {
      "totalScanCount": 411,
      "snName": "2"
    },
    {
      "totalScanCount": 203,
      "snName": "3"
    },{
      "totalScanCount": 705,
      "snName": "1"
    },
    {
      "totalScanCount": 411,
      "snName": "2"
    },
    {
      "totalScanCount": 203,
      "snName": "3"
    },
  ],
  "fansNumRankList": [  //
    {
      "totalFans": 160,
      "displayName": ""
    },
    {
      "totalFans": 130,
      "displayName": ""
    },{
      "totalFans": 160,
      "displayName": ""
    },
    {
      "totalFans": 130,
      "displayName": ""
    },{
      "totalFans": 160,
      "displayName": ""
    },
    {
      "totalFans": 130,
      "displayName": ""
    },{
      "totalFans": 160,
      "displayName": ""
    },
    {
      "totalFans": 130,
      "displayName": ""
    },{
      "totalFans": 160,
      "displayName": ""
    },
    {
      "totalFans": 130,
      "displayName": ""
    },
  ],
  "fansScanNumRankList": [ //
    {
      "totalScanCount": 800,
      "displayName": ""
    },
    {
      "totalScanCount": 519,
      "displayName": ""
    },{
      "totalScanCount": 800,
      "displayName": ""
    },
    {
      "totalScanCount": 519,
      "displayName": ""
    },{
      "totalScanCount": 800,
      "displayName": ""
    },
    {
      "totalScanCount": 519,
      "displayName": ""
    },{
      "totalScanCount": 800,
      "displayName": ""
    },
    {
      "totalScanCount": 519,
      "displayName": ""
    },{
      "totalScanCount": 800,
      "displayName": ""
    },
    {
      "totalScanCount": 519,
      "displayName": ""
    },{
      "totalScanCount": 519,
      "displayName": ""
    },{
      "totalScanCount": 519,
      "displayName": ""
    },
  ]
}
export default {
  name: "SellerRank",
  data(){
    return{
      loading:false,
      saleZoneCode: sessionStorage.getItem('isAllSaleZone') == 1 ? null : sessionStorage.getItem('saleZoneCode'),
      search:{
        saleZoneCode: sessionStorage.getItem('isAllSaleZone') == 1 ? null : sessionStorage.getItem('saleZoneCode'),
        provinceCode:null
      },
      saleZone:[],
      saleZoneProv:[],
      fansScanSnNumRankList:[],
      fansNumRankList:[],
      fansScanNumRankList:[],
      showBtn:false,
      listLoading:false,
      showMore1:false,
      showMore2:false,
      showMore3:false,
      showMore4:false,
    }
  },
  mounted() {
    if(this.saleZoneCode === null){
      this.showBtn = true
    }
    this.getSaleZone();
    if(this.saleZoneCode === null){
      this.handleClickALL('allZone');
    }else {
      this.getSellerRank()
    }
  },
  methods:{
    handleClickALL(type){
      this.search.saleZoneCode = null
      this.search.provinceCode = null
      let params = {
        saleZoneCode:type === 'allZone'?'all':null,
        provinceCode:type === 'allProv'?'all':null,
      }
      this.$request.post('/dataStats/statHbsSeller/fansDs/rank', params, false, (res) => {
        if (res.code == '200') {
          this.fansScanSnNumRankList = res.data.fansScanSnNumRankList.length?res.data.fansScanSnNumRankList: RankData.fansScanSnNumRankList
          this.fansNumRankList = res.data.fansNumRankList.length?res.data.fansNumRankList: RankData.fansNumRankList
          this.fansScanNumRankList = res.data.fansScanNumRankList.length?res.data.fansScanNumRankList: RankData.fansScanNumRankList
        }
      })
    },
    handleSaleZoneCodeChange(saleZone){
      this.search.provinceCode = null
      this.$request.post('/api/saleZone/saleZoneProv', {saleZone}, false, (res) => {
        if (res.code == '200') {
          this.saleZoneProv = res.data || []
        }
      })
    },
    getSaleZone() {
      this.$request.post('/api/saleZone/userSzList', {}, true, (res) => {
        if (res.code == '200') {
          this.saleZone = res.data || []
          if (this.isAllSaleZone == 1) {
          } else {
            this.search.saleZoneCode = this.saleZoneCode
          }
        }
      })
    },
    getSellerRank() {
      let params = {
        saleZoneCode:this.search.saleZoneCode,
        provCode:this.search.provinceCode,
      }
      if(!params.saleZoneCode&&!params.provCode){
        this.$message.warning("");
        return
      }
      this.$request.post('/dataStats/statHbsSeller/fansDs/rank', params, false, (res) => {
        if (res.code == '200') {

          if(!res.data){
            res.data = RankData
          }
          this.fansScanSnNumRankList = res.data.fansScanSnNumRankList
          this.fansNumRankList = res.data.fansNumRankList
          this.sellerShopFansNumRank = res.data.sellerShopFansNumRank
          this.fansScanNumRankList = res.data.fansScanNumRankList
        }
      })
    },
  }
}
</script>

<style lang="scss" scoped>
.seller-rank-container{
  .form-container{
    display: flex;
  }
  .rank-form{
    width: 300px;
    border: 1px solid #e1e1e1;
    margin-left: 30px;
    margin-bottom: 30px;
    height: fit-content;
    .title{
      height: 40px;
      line-height: 40px;
      text-align: center;
      font-size: 14px;
      background-color: #e1e1e1;
    }
    .more{
      height: 40px;
      line-height: 40px;
      text-align: center;
      font-size: 14px;
      cursor: pointer;
    }
  }
}
</style>



